import React from "react";

import arrowIcon from "../../assets/ico/ARROW_ICON.svg";
import crossIcon from "../../assets/ico/CROSS_ICON.svg";
import cartIcon from "../../assets/ico/CART_ICON.svg";
import minusIcon from "../../assets/ico/MINUS_ICON.svg";
import plusIcon from "../../assets/ico/PLUS_ICON.svg";
import deleteIcon from "../../assets/ico/DELETE_ICON.svg";

interface IArrowIconProps {
  className?: string;
}

export const ArrowIcon: React.FC<IArrowIconProps> = React.memo(
  ({ className }) => {
    return (
      <img
        className={`${"arrow-icon"} ${className ? className : ""}`}
        width={10}
        height={5}
        src={arrowIcon}
        alt="arrow"
      />
    );
  }
);

export const CrossIcon: React.FC = () => {
  return (
    <img
      className="cross-icon"
      width={15}
      height={15}
      src={crossIcon}
      alt="cross"
    />
  );
};

export const CartIcon: React.FC = () => {
  return <img className="cart-icon" src={cartIcon} alt="cart" />;
};

export const MinusIcon: React.FC = () => {
  return <img className="minus-icon" src={minusIcon} alt="minus" />;
};

export const PlusIcon: React.FC = () => {
  return <img className="plus-icon" src={plusIcon} alt="plus" />;
};

export const DeleteIcon: React.FC = () => {
  return <img className="delete-icon" src={deleteIcon} alt="delete" />;
};
