import React, { useCallback, useRef, useState, useEffect } from "react";
import CSSTransition from "react-transition-group/CSSTransition";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { selectActiveBrand } from "../../redux/filter/selectors";
import { setActiveBrand } from "../../redux/filter/slice";

import { ArrowCircle } from "..";

import { brandsList } from "../../arrays";

import s from "./Brands.module.scss";

export const Brands: React.FC = React.memo(() => {
  const dispatch = useAppDispatch();
  const activeBrand = useAppSelector(selectActiveBrand);

  const [isVisiblePopup, setIsVisiblePopup] = useState<boolean>(false);

  const popupRef = useRef<HTMLDivElement>(null);
  const brandsRef = useRef<HTMLDivElement>(null);

  const handleShowBrands = () => {
    setIsVisiblePopup((prevState) => !prevState);
  };

  const handleActiveBrand = useCallback((item: string) => {
    dispatch(setActiveBrand(item));
    setIsVisiblePopup(false);
  }, []);

  useEffect(() => {
    const onClickBody = (event: MouseEvent) => {
      if (
        brandsRef.current &&
        !brandsRef.current.contains(event.target as Node)
      ) {
        setIsVisiblePopup(false);
      }
    };

    document.body.addEventListener("click", onClickBody);

    return () => {
      document.body.removeEventListener("click", onClickBody);
    };
  }, []);

  const popupJSX: JSX.Element = (
    <CSSTransition
      in={isVisiblePopup}
      timeout={300}
      classNames={{
        enter: s.popupEnter,
        enterActive: s.popupEnterActive,
        exit: s.popupExit,
        exitActive: s.popupExitActive,
      }}
      unmountOnExit
      nodeRef={popupRef}
    >
      <div ref={popupRef} className={s.popup}>
        <ul className={s.list}>
          {brandsList.map((item) => (
            <li
              onClick={() => handleActiveBrand(item)}
              key={item}
              className={`${s.listItem} ${
                activeBrand === item ? s.active : ""
              }`}
            >
              {item}
            </li>
          ))}
        </ul>
      </div>
    </CSSTransition>
  );

  return (
    <div ref={brandsRef} className={s.root}>
      <button onClick={handleShowBrands} className={s.showBrandsButton}>
        {isVisiblePopup ? "Hide Brands" : "Show Brands"}
        <ArrowCircle className={isVisiblePopup ? s.active : ""} />
      </button>
      {popupJSX}
    </div>
  );
});
