import React from "react";

import { ArrowIcon } from "./Icons";

interface IArrowCircleProps {
  className?: string;
}

export const ArrowCircle: React.FC<IArrowCircleProps> = React.memo(
  ({ className }) => {
    return (
      <span className={`${className ? className : ""} arrow-circle`}>
        <ArrowIcon />
      </span>
    );
  }
);
