import { forwardRef } from "react";

import s from "./ConfirmationModal.module.scss";

interface IPopupConfirm {
  title: string;
  onConfirm: () => void;
  onCancel: () => void;
}

export const ConfirmationModal = forwardRef<HTMLDivElement, IPopupConfirm>(
  ({ title, onConfirm, onCancel }, ref) => {
    return (
      <div ref={ref} className={s.overlay}>
        <div className={s.body}>
          <h3 className={s.title}>{title}</h3>
          <div className={s.buttons}>
            <button
              onClick={onCancel}
              className={`${s.button} ${s.buttonCancel}`}
            >
              Cancel
            </button>
            <button
              onClick={onConfirm}
              className={`${s.button} ${s.buttonConfirm}`}
            >
              Confirm
            </button>
          </div>
        </div>
      </div>
    );
  }
);
