import { createSlice } from "@reduxjs/toolkit";

import type { ICartSliceState } from "./types";
import type { TProduct } from "../../types";

const initialState: ICartSliceState = {
  items: [],
  totalPrice: 0,
};

const calculateTotalPrice = (items: TProduct[]) => {
  return items.reduce(
    (total: number, item) => total + item.price * item.count,
    0
  );
};

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    toggleCartItem: (state, action) => {
      const item = action.payload;
      const existingItem = state.items.findIndex((i) => i.id === item.id);
      if (existingItem !== -1) {
        state.items.splice(existingItem, 1);
      } else {
        state.items.push({ ...item, count: 1 });
      }
      state.totalPrice = calculateTotalPrice(state.items);
    },
    plusCartItem: (state, action) => {
      const itemId = action.payload;
      const item = state.items.find((item) => item.id === itemId);
      if (item) {
        item.count += 1;
      }
      state.totalPrice = calculateTotalPrice(state.items);
    },
    minusCartItem: (state, action) => {
      const itemId = action.payload;
      const item = state.items.find((item) => item.id === itemId);
      if (item) {
        item.count -= 1;
      }
      if (item && item.count <= 1) {
        item.count = 1;
      }
      state.totalPrice = calculateTotalPrice(state.items);
    },
    removeCartItem: (state, action) => {
      const itemId = action.payload;
      state.items = state.items.filter((item) => item.id !== itemId);
      state.totalPrice = calculateTotalPrice(state.items);
    },
    clearCart: (state) => {
      state.items = [];
      state.totalPrice = 0;
    },
  },
});

export const {
  toggleCartItem,
  minusCartItem,
  removeCartItem,
  clearCart,
  plusCartItem,
} = cartSlice.actions;

export default cartSlice.reducer;
