import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

import type { TProduct } from "../../types/index";

export const fetchProducts = createAsyncThunk<
  TProduct[],
  Record<string, string>
>("products/fetchProducts", async (params) => {
  const { activeBrand, brand, sortBy, order } = params;

  const URL: string = `https://63aca423da81ba976189a2ec.mockapi.io/clothing?${
    activeBrand !== "All" ? brand : ""
  }&${sortBy}&${order}`;

  const { data } = await axios.get<TProduct[]>(URL);

  return data;
});
