import React, { useCallback, useRef, useState } from "react";
import CSSTransition from "react-transition-group/CSSTransition";
import { useInView } from "react-intersection-observer";

import { useAppDispatch } from "../../hooks";
import {
  minusCartItem,
  plusCartItem,
  removeCartItem,
} from "../../redux/cart/slice";

import {
  ProductPrice,
  ProductTitle,
  ProductBrand,
  ConfirmationModal,
} from "..";
import { DeleteIcon, MinusIcon, PlusIcon } from "../ui/Icons";

import type { TProduct } from "../../types";

import s from "./CartItem.module.scss";

export const CartItem: React.FC<TProduct> = React.memo(
  ({ id, title, price, brand, count, imageUrl }) => {
    const dispatch = useAppDispatch();

    const confirmationModalRef = useRef<HTMLDivElement>(null);

    const [showConfirmation, setShowConfimation] = useState<boolean>(false);

    const [ref, inView] = useInView({
      threshold: 0.5,
      delay: 100,
      rootMargin: "100px",
      triggerOnce: true,
    });

    const onRemoveItem = useCallback(() => {
      setShowConfimation(true);
    }, []);

    const onCancel = useCallback(() => {
      setShowConfimation(false);
    }, []);

    const onPlusItem = useCallback(() => {
      dispatch(plusCartItem(id));
    }, [dispatch, id]);

    const onMinusItem = useCallback(() => {
      dispatch(minusCartItem(id));
    }, [dispatch, id]);

    const onConfirmRemoveItem = useCallback(() => {
      dispatch(removeCartItem(id));
      setShowConfimation(false);
    }, [dispatch, id]);

    const confirmationModalJSX: JSX.Element = (
      <CSSTransition
        in={showConfirmation}
        classNames={{
          enter: s.modalEnter,
          enterActive: s.modalEnterActive,
          exit: s.modalExit,
          exitActive: s.modalExitActive,
        }}
        timeout={200}
        unmountOnExit
        nodeRef={confirmationModalRef}
      >
        <ConfirmationModal
          ref={confirmationModalRef}
          title="ARE YOU SURE YOU WANT TO REMOVE THIS PRODUCT?"
          onConfirm={onConfirmRemoveItem}
          onCancel={onCancel}
        />
      </CSSTransition>
    );

    return (
      <li ref={ref} className={s.root}>
        <div className={s.image}>
          {inView && <img src={imageUrl} alt={title} />}
        </div>
        <div className={s.info}>
          <ProductTitle value={title} />
          <ProductPrice value={price} />
          <ProductBrand value={brand} />
        </div>
        <div className={s.counter}>
          <button onClick={onMinusItem} className={s.button}>
            <MinusIcon />
          </button>
          <span className={s.count}>{count}</span>
          <button onClick={onPlusItem} className={s.button}>
            <PlusIcon />
          </button>
        </div>
        <button onClick={onRemoveItem} className={s.deleteButton}>
          <DeleteIcon />
        </button>
        {confirmationModalJSX}
      </li>
    );
  }
);
