import React from "react";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { selectFilter } from "../../redux/filter/selectors";
import { fetchProducts } from "../../redux/products/asyncAction";
import { selectProducts, selectStatus } from "../../redux/products/selectors";

import {
  SectionTitle,
  ProductCard,
  FetchError,
  Preloader,
} from "../../components";

import s from "./Home.module.scss";

const Home: React.FC = () => {
  const dispatch = useAppDispatch();
  const { activeBrand, activeSort } = useAppSelector(selectFilter);
  const products = useAppSelector(selectProducts);
  const requestStatus = useAppSelector(selectStatus);

  React.useEffect(() => {
    const getProducts = async () => {
      let sortBy: string;

      if (
        activeSort === "Price High To Low" ||
        activeSort === "Rating High To Low"
      ) {
        sortBy = `sortBy=${activeSort
          ?.replace(" High To Low", "")
          .toLowerCase()}`;
      } else {
        sortBy = `sortBy=${activeSort
          ?.replace(" Low To High", "")
          .toLowerCase()}`;
      }

      const order: string = `order=${
        activeSort?.includes("High To Low") ? `desc` : `asc`
      }`;

      const brand: string = `brand=${activeBrand}`;

      dispatch(
        fetchProducts({
          activeBrand,
          sortBy,
          order,
          brand,
        })
      );
    };

    getProducts();
  }, [activeSort, activeBrand]);

  const productsJSX: JSX.Element = (
    <ul className={s.productsList}>
      {requestStatus === "loading"
        ? Array.from({ length: 8 }).map((_, index) => <Preloader key={index} />)
        : products.map((obj) => <ProductCard key={obj.id} {...obj} />)}
    </ul>
  );

  const sectionTitleJSX: JSX.Element =
    requestStatus === "error" ? (
      <FetchError />
    ) : (
      <SectionTitle value={activeBrand.toUpperCase()} />
    );

  return (
    <div className={s.page}>
      <section className={s.productsSection}>
        <div className={`${s.container} container`}>
          {sectionTitleJSX}
          {productsJSX}
        </div>
      </section>
    </div>
  );
};

export default Home;
