import React from "react";
import { useInView } from "react-intersection-observer";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { toggleCartItem } from "../../redux/cart/slice";
import { isItemInCart } from "../../redux/cart/selectors";

import { ProductPrice, ProductBrand, ProductTitle } from "..";

import type { TProduct } from "../../types";

import s from "./ProductCard.module.scss";

import addedToCartIcon from "../../assets/ico/ADDED_TO_CART_ICON.svg";
import addToCartIcon from "../../assets/ico/ADD_TO_CART_ICON.svg";

export const ProductCard: React.FC<TProduct> = React.memo(
  ({ id, price, title, brand, imageUrl }) => {
    const dispatch = useAppDispatch();
    const isItemAdded = useAppSelector<boolean>(isItemInCart(id));

    const [ref, inView] = useInView({
      threshold: 0.1,
      delay: 100,
      rootMargin: "100px",
      triggerOnce: true,
    });

    const handleToggleCartItem = () => {
      const item = { id, price, title, brand, imageUrl };
      dispatch(toggleCartItem(item));
    };

    const buttonImgSrc = isItemAdded ? addedToCartIcon : addToCartIcon;

    return (
      <li ref={ref} className={s.root}>
        <div className={s.image}>
          {inView && <img src={imageUrl} alt={title} />}
        </div>
        <div className={s.info}>
          <ProductTitle value={title} />
          <div className={s.details}>
            <div className={s.detailsWrapp}>
              <ProductPrice value={price} />
              <ProductBrand value={brand} />
            </div>
            <button
              onClick={handleToggleCartItem}
              className={s.addToCartButton}
            >
              <img
                className="cartIcon"
                src={buttonImgSrc}
                alt={"add/remove from cart"}
              />
            </button>
          </div>
        </div>
      </li>
    );
  }
);
