import React from "react";

import { SectionTitle } from "..";

import s from "./FetchError.module.scss";

export const FetchError: React.FC = () => {
  return (
    <div className={s.root}>
      <SectionTitle value="PRODUCTS NOT FOUND" />
      <p className={s.message}>
        Reload the page or try changing the filtering or search data
      </p>
    </div>
  );
};
