import React, { useRef, useState, useEffect } from "react";
import CSSTransition from "react-transition-group/CSSTransition";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { selectActiveSort } from "../../redux/filter/selectors";
import { setActiveSort } from "../../redux/filter/slice";

import { ArrowCircle } from "..";

import { sortList } from "../../arrays";

import s from "./Sort.module.scss";

export const Sort: React.FC = () => {
  const dispatch = useAppDispatch();
  const activeSort = useAppSelector(selectActiveSort);

  const sortRef = useRef<HTMLDivElement>(null);
  const popupRef = useRef<HTMLDivElement>(null);

  const [isVisiblePopup, setIsVisiblePopup] = useState<boolean>(false);

  const handleOpenSort = () => {
    setIsVisiblePopup((prevState) => !prevState);
  };
  const handleActiveSort = (item: string) => {
    dispatch(setActiveSort(item));
    setIsVisiblePopup(false);
  };

  useEffect(() => {
    const onClickBody = (event: MouseEvent) => {
      if (sortRef.current && !sortRef.current.contains(event.target as Node)) {
        setIsVisiblePopup(false);
      }
    };

    document.body.addEventListener("click", onClickBody);

    return () => {
      document.body.removeEventListener("click", onClickBody);
    };
  }, []);

  const popupJSX: JSX.Element = (
    <CSSTransition
      nodeRef={popupRef}
      timeout={350}
      in={isVisiblePopup}
      classNames={{
        enter: s.popupEnter,
        enterActive: s.popupEnterActive,
        exit: s.popupExit,
        exitActive: s.popupExitActive,
      }}
      unmountOnExit
    >
      <div ref={popupRef} className={s.popup}>
        <ul className={s.list}>
          {sortList.map((item) => (
            <li
              onClick={() => handleActiveSort(item)}
              key={item}
              className={`${s.listItem} ${activeSort === item ? s.active : ""}`}
            >
              {item}
            </li>
          ))}
        </ul>
      </div>
    </CSSTransition>
  );

  const arrowCircleClassNames = isVisiblePopup ? s.active : "";

  return (
    <div ref={sortRef} className={s.root}>
      <button onClick={handleOpenSort} className={s.showSortButton}>
        Sort By: <ArrowCircle className={arrowCircleClassNames} />
      </button>
      {popupJSX}
    </div>
  );
};
